.Services {
    width: 98%;
    margin: 0% auto;
    margin-top: 5%;
    color: white;
}

.Services-Header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Services-Header p {
    color: #B9FD50;
    font-size: 1rem;
    margin-bottom: -2%;
}

.Services-Header h1 {
    font-size: 3.375rem;
    margin-bottom: 0%;
    font-weight: bold;
    text-align: center;
}

.Services-Header-Grid {
    /* display: grid;
    grid-template-columns: auto auto auto auto auto auto auto;
    grid-template-rows: auto auto; */
    display: flex;
    flex-wrap: wrap;
    justify-self: center;
    gap: 1%;
    margin-top: 1%;
    justify-content: center;
    text-align: center;
    width: 80%;
}

.Services-Header-Grid span {
    color: white;
    padding: 0.6875rem 2.1875rem 0.75rem 1.75rem;
    border: 1px solid white;
    border-radius: 8px;
    margin-top: 1%;
}

.Services-Header-Grid span:hover {
    background-color: #B9FD50;
    color: #111204;
    border: 1px solid #B9FD50;
    cursor: pointer;
}

.Hidden-Services {
    display: none;
}

.Show-Services {
    display: block;
}

.Services-Header button {
    background: none;
    border: none;
    color: white;
    font-size: 1.125rem;
    font-weight: lighter;
    margin-top: 4%;
    width: 12%;
    cursor: pointer;
}

.Services-Header button:active {
    background-image: url('./GreenBuffer.png');
    background-size: 15% 100%;
    background-repeat: no-repeat;
    background-position: left;
    /* background-position-x: 10px; */
    cursor: progress;
}

.btn.Active {
    color: #B9FD50;
}

.Services-Content {
    display: grid;
    grid-template-columns: auto auto;
    justify-self: center;
    gap: 5%;
    margin-top: 8%;
    width: 80%
}

.Services-Content-Left-Div p {
    color: #B9FD50;
    margin-bottom: -5%;
}

.Services-Content-Left-Div h1 {
    font-size: 3.375rem;
    font-weight: bold;
}

.Services-Content-Right-Div p {
    font-size: 1.5rem;
    font-weight: lighter;
    margin-top: 12%;
}

.Services-Content-Right-Div button {
    margin-left: 0%;
    margin-top: 2%;
    cursor: pointer;
}

.Services-Description {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    justify-self: center;
    /* gap: 50px; */
    margin-top: 8%;
    width: 80%;
}

.Services-Description-Top-Border {
    border-top: 1px solid rgba(185, 253, 80, 0.09);
}

.Services-Description-Right-Border {
    border-right: 1px solid rgba(185, 253, 80, 0.09);
}

.Services-Description-Top-Right-Border {
    border-top: 1px solid rgba(185, 253, 80, 0.09);
    border-right: 1px solid rgba(185, 253, 80, 0.09);
}

.Services-Description-First-Row {
    padding: 0rem 1.875rem 1.875rem 0rem;
}

.Services-Description-Second-Row {
    padding: 3.125rem 1.875rem 1.875rem 0rem;
}

.Services-Description-Third-Row {
    padding: 3.125rem 1.875rem 0rem 0rem;
}

.Services-Description-Second-Column,
.Services-Description-Third-Column {
    padding-left: 1.875rem;
}

.Services-Description h1 {
    font-size: 1.25rem;
    font-weight: 500;
}

.Services-Description p {
    font-size: 1rem;
    color: #9593A4;
}

.Services-Description img {
    height: 2.5rem;
    width: 2.5rem;
}
@media (max-width: 1440px) {
    .Services-Header h1{
        font-size: 48px;
    }
    .Services-Description h1{
        font-size: 26px;
    }
    .Services-Description p{
        font-size: 18px;
    }
}
@media (min-width: 1024px) and (max-width: 1439px) {
    .Services-Content-Left-Div h1{
        font-size: clamp(42px, 4vw, 54px);
    }
    .Services-Description h1{
        font-size: clamp(22px, 2vw, 26px);
    }
    .Services-Description p{
        font-size: clamp(16px, 1.2vw, 18px);
    }
}
@media (max-width: 1024px) {
    html {
        font-size: 14px;
    }
    .Services-Header h1{
        font-size: 42px;
    }
    .Services-Header-Grid {
        width: 92%;
    }
    .Hidden-Services-1024 {
        display: none;
    }

    .Show-Services-1024 {
        display: block;
    }

    .Services-Content {
        width: 90%;
    }

    .Services-Content-Left-Div h1 {
        font-size: 42px;
    }

    .Services-Content-Right-Div p {
        font-size: 21px;
    }

    .Services-Description {
        width: 90%;
    }
    .Services-Description h1 {
        font-size: 22px;
    }
    
    .Services-Description p {
        font-size: 16px;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    html {
        font-size: 13px;
    }

    .Services-Header button {
        width: 15%;
    }

    .Services-Content-Left-Div h1 {
        font-size: 2.3rem;
    }

    .Services-Content-Right-Div p {
        font-size: 1.4rem;
        margin-top: 10%;
    }
}

@media (max-width: 768px) {
    html {
        font-size: 12px;
    }
    .Services-Header h1{
        font-size: 36px;
    }
    .Services-Header-Grid {
        width: 90%;
    }

    .Hidden-Services-768 {
        display: none;
    }

    .Show-Services-1024 {
        display: block;
    }

    .Services-Header button {
        width: 15%;
    }

    .Services-Content {
        width: 90%;
    }

    .Services-Content-Left-Div h1 {
        font-size: 2.5rem;
    }

    .Services-Content-Right-Div p {
        font-size: 1.4rem;
        margin-top: 12%;
    }

    .Services-Description {
        width: 90%;
    }

}

@media (min-width: 425px) and (max-width: 767px) {
    .Services-Header h1{
        font-size: 30px;
    }
    .Services-Header p{
        font-size: 14px;
    }
    .Services-Header button{
        display: none;
    }
    .Services-Content {
        grid-template-columns: auto;
        grid-template-rows: auto auto;
        gap: 0%;
    }
    .Services-Content-Left-Div p{
        margin-bottom: 0%;
    }
    .Services-Content-Right-Div p {
        font-size: 1.5rem;
        margin-top: 0%;
    }
    .Services-Description{
        display: grid;
        grid-template-columns: auto;
    }
    .Services-Description-Mobile-View h1{
        font-size: 24px;
    }
    .Services-Description-Mobile-View p{
        font-size: 14px;
    }
    .Services-Description-Mobile-View{
        border: none;
        padding: 5% 0%;
        border-bottom: 1px solid rgba(185, 253, 80, 0.09);
    }
    .Services-Description-Mobile-View:last-child{
        border: none;
    }
}

@media (max-width: 426px) {
    .Services-Header h1 {
        font-size: 2rem;
    }

    .Services-Header button {
        display: none;
    }

    .Services-Content {
        grid-template-columns: auto;
        grid-template-rows: auto auto;
        gap: 0%;
    }

    .Services-Content-Right-Div p {
        font-size: 1.5rem;
        margin-top: 0%;
    }
    .Services-Description{
        display: grid;
        grid-template-columns: auto;
    }
    .Services-Description-Mobile-View h1{
        font-size: 1.8rem;
    }
    .Services-Description-Mobile-View{
        border: none;
        padding: 10% 0%;
        border-bottom: 1px solid rgba(185, 253, 80, 0.09);
    }
    .Services-Description-Mobile-View:last-child{
        border: none;
    }
}