.Portfolio{
    background-color: white;
    width: 90%;
    color: #111204;
    margin: 0 auto;
    /* margin-top: -300px; */
}
.Portfolio-Header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 3%;
}
.Portfolio-Header h1{
    font-size: 2.875rem;
    margin-bottom: 0%;
    font-weight: bold;
    text-align: center;
}
.Portfolio-Header p{
    font-size: 1.125rem;
    text-align: center;
}
.Portfolio-header-Para-Second-Line{
    margin-top: -1%;
}
.Portfolio-Header button{
    /* width: 10%; */
    padding: 1.125rem 2.5rem;
    font-size: 1.125rem;
    background: none;
    border: 1px solid #111204;
    border-radius: 8px;
    margin-top: 2%;
    cursor: pointer;
}
.Portfolio-Header button:active{
    background-image: url('./Buffer.png');
    background-size: 15% 40%;
    background-repeat: no-repeat;
    background-position: left;
    background-position-x: 8%;
}
.Portfolio-Content{
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto auto auto;
    justify-self: center;
    gap: 3%;
    margin-top: 8%;
}
.Portfolio-Content img{
    height: auto;
    width: 100%;
    margin-top: 3%;
    max-width: 100%;
}
.Portfolio-Content-Div1{
    margin-top: 50%;
}
.Hidden-Images{
    display: none;
}
.Show-Images{
    display: block;
}
@media (max-width: 1440px) {
    .Portfolio-Header h1{
        font-size: 48px;
    }
    .Portfolio-Header p{
        font-size: 18px;
    }
}
@media (max-width: 1024px) {
    .Portfolio-Header h1{
        font-size: 42px;
    }
    .Portfolio-Header p{
        font-size: 16px;
    }
}
@media (max-width: 768px) {
    .Portfolio-Header h1{
        font-size: 36px;
    }
    .Portfolio-Header p{
        font-size: 16px;
    }
}
@media (min-width: 425px) and (max-width: 767px) {
    .Portfolio-Header h1{
        font-size: 30px;
    }
    .Portfolio-Header p{
        font-size: 14px;
    }
}

@media (max-width: 426px) {
    .Portfolio{
        width: 100%;
    }
    .Portfolio-Header h1{
        font-size: 2rem;
    }
    .Portfolio-Header p{
        font-size: 1rem;
        width: 90%;
    }
    .Portfolio-header-Para-Second-Line{
        display: none;
    }
    .Portfolio-Header button{
        font-size: 1rem;
    }
}