.Header-Content {
  /* background-image: url('./Ellipse 3.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; */
  /* margin-bottom: 1000px; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 4%;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.Header-Content h1 {
  font-size: 6.875rem;
  font-weight: bold;
  text-align: center;
  /* width: 75%; */
}

.Header-Content-H1-Div {
  position: relative;
}

.BigStar {
  position: absolute;
  left: 96%;
  bottom: 100%;
  width: 12%;
  height: 60%;
}

.Header-Content-H1-Div h1 {
  margin-top: -5%;
}

.Header-Content span {
  color: #B9FD50;
}

.SmallStar {
  width: 10px;
  height: 10px;
  position: absolute;
  left: 4%;
  bottom: -65%;
}

.Header-Content-Para-Div {
  position: relative;
}

.Header-Content p {
  font-size: 1.125rem;
  text-align: center;
  margin-top: -1%;
}

.MediumStar {
  height: 15px;
  width: 15px;
  position: absolute;
  left: 92%;
  bottom: -95%;
}

.Header-Content button {
  font-size: 1rem;
  font-weight: 500;
  margin-left: 0%;
  margin-top: 2%;
  padding: 1rem;
}

.Header-Content-Trusted {
  display: flex;
  justify-content: space-around;
  align-items: center;
  justify-self: center;
  position: relative;
  margin-bottom: 2%;
}

.Header-Content-Trusted p {
  font-size: 1rem;
  font-weight: bold;
}

.Trusted-Left-Line {
  position: absolute;
  background-color: #B9FD50;
  width: 50px;
  height: 2px;
  left: -30%;
  top: 50%;
}

.Trusted-Right-Line {
  position: absolute;
  background-color: #B9FD50;
  width: 50px;
  height: 2px;
  right: -30%;
  top: 50%;
}

.Header-Content-Brands {
  background-color: #222222;
  border: none;
  border-radius: 22px;
  width: 100%;
  height: 7%;
  box-sizing: border-box;
  justify-self: center;
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  align-content: center;
  gap: 30px;
  padding: 0px 89.5px;
}

.Header-Content-Brands img {
  height: 100%;
  width: 100%;
}

.Header-Content-Working {
  display: grid;
  grid-template-columns: auto auto;
  justify-self: center;
  gap: 7%;
  margin-top: 8%;
  width: 90%;
}

.Header-Content-Working-Left-Div,
.Header-Content-Working-Right-Div {
  display: flex;
  flex-direction: column;
  gap: 10%;
}

.Header-Content-Working-Left-Div h2 {
  font-size: 3.375rem;
  font-weight: bold;
  text-align: left;
  /* line-height: 58px; */
  margin: 0%;
}

.Header-Content-Working-Left-Div p {
  font-size: 1rem;
  text-align: left;
  color: #B9FD50;
  margin: 0%;
}

.Header-Content-Working-Right-Div p {
  text-align: left;
  font-size: 1.5rem;
  font-weight: lighter;
  padding-top: 6%;
}

.Header-Content-Working-Right-Div button {
  margin-left: 0%;
}

.Header-Content-Process {
  display: grid;
  grid-template-columns: auto auto auto;
  justify-self: center;
  width: 90%;
  box-sizing: border-box;
  gap: 50px;
  margin-top: 10%;
}

.Header-Content-Process-Left-Div,
.Header-Content-Process-Middle-Div,
.Header-Content-Process-Right-Div {
  width: 100%;
}

.Rocket-Icon-Div,
.Edit-Icon-Div {
  display: flex;
  justify-content: left;
  align-items: center;
}

.RocketIcon,
.ReviseIcon,
.EditIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 40px;
  background-color: #B9FD50;
  height: 80px;
  width: 80px;
}

.RocketIcon img,
.ReviseIcon img,
.EditIcon img {
  color: #111204;
  height: 46px;
  width: 46px;
}

.Arrow {
  position: relative;
  width: 75%;
  /* Width of the arrow shaft */
  height: 2px;
  /* Height of the arrow shaft */
  background-color: #B9FD50;
  /* Color of the arrow */
}

/* Arrowhead */
.Arrow::after {
  content: '';
  position: absolute;
  right: 0px;
  /* Position the arrowhead at the end of the shaft */
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  /* Rotate to form the arrowhead */
  width: 8px;
  /* Size of the arrowhead */
  height: 8px;
  border-top: 2px solid #B9FD50;
  /* Color and thickness of the arrowhead */
  border-right: 2px solid #B9FD50;
}

.Header-Content-Process-Left-Div h3,
.Header-Content-Process-Middle-Div h3,
.Header-Content-Process-Right-Div h3 {
  font-size: 1.625rem;
  font-weight: medium;
}

.Header-Content-Process-Left-Div p,
.Header-Content-Process-Middle-Div p,
.Header-Content-Process-Right-Div p {
  font-size: 1.125rem;
  width: 90%;
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .Header-Content h1 {
    font-size: clamp(83px, 8vw, 110px);
  }

  .Header-Content p {
    font-size: clamp(16px, 2vw, 18px);
  }

  .Header-Content-Working-Left-Div h2 {
    font-size: clamp(42px, 4vw, 54px);
  }

  .Header-Content-Process-Left-Div h3,
  .Header-Content-Process-Middle-Div h3,
  .Header-Content-Process-Right-Div h3 {
    font-size: clamp(22px, 2vw, 26px);
    height: 60px;
    box-sizing: border-box;
    margin-bottom: 0px;
  }

  .Header-Content-Process-Left-Div p,
  .Header-Content-Process-Middle-Div p,
  .Header-Content-Process-Right-Div p {
    font-size: clamp(16px, 1.2vw, 18px);
  }
}

