  }

  .Header-Content-Process-Left-Div h3,
  .Header-Content-Process-Middle-Div h3,
  .Header-Content-Process-Right-Div h3 {
    font-size: 22px;
  }

  .Header-Content-Process-Left-Div p,
  .Header-Content-Process-Middle-Div p,
