html {
    font-size: 16px;
}

.Pricing {
    width: 90%;
    margin: 0 auto;
    margin-top: 8%;
    color: white;
}

.Pricing-Header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Pricing-Header h4 {
    color: #B9FD50;
    font-size: 1rem;
    font-weight: 400;
    margin-bottom: -2%;
}

.Pricing-Header h1 {
    font-size: 3rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 0.5%;
}

.Pricing-Header p {
    font-size: 1.125rem;
    text-align: center;
}

.Pricing-Plans {
    display: grid;
    grid-template-columns: 30% 30% 30%;
    gap: 2%;
    place-content: center;
    margin-top: 8%;
}

.Pricing-Plan-Lines {
    width: 100%;
    border: 1px solid rgba(185, 253, 80, 0.37);
}

.Pricing-Plan-One,
.Pricing-Plan-Two,
.Pricing-Plan-Three {
    border: 1px solid rgba(185, 253, 80, 0.37);
    border-radius: 1rem;
    /* width: 100%; */
    padding: 2.5rem;
    background-color: #222222;
    box-sizing: border-box;
}

.Pricing-Plan-Header h1 {
    font-size: 2.125rem;
    font-weight: 700;
    text-align: left;
    vertical-align: middle;
}

.Pricing-Plan-Header-Feature-1 {
    background-color: #B9FD50;
    font-size: 0.875rem;
    font-weight: lighter;
    color: black;
    padding: 0.625rem 1.25rem;
    border-radius: 8px;
    border: none;
    text-align: center;
    vertical-align: middle;
}

.Pricing-Plan-Header-Feature-2 {
    background-color: white;
    font-size: 0.875rem;
    font-weight: lighter;
    color: black;
    padding: 0.625rem 1.25rem;
    border-radius: 8px;
    border: none;
    text-align: center;
    vertical-align: middle;
}

.Pricing-Plan-Header p {
    font-size: 0.875rem;
    margin-bottom: 12%;
}

.Pricing-Plan-Price h1 {
    color: #B9FD50;
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: -3%;
}

.Pricing-Plan-Price p {
    font-size: 0.875rem;
    margin-bottom: 12%;
}

.Pricing-Plan-Benefits p {
    font-size: 1.3125rem;
    margin-left: 5%;
    display: inline-block;
    vertical-align: middle;
    margin-top: 0%;
}

.Pricing-Plan-Benefits img {
    height: 5%;
    width: 5%;
    display: inline-block;
    vertical-align: middle;
    margin-top: -6%;
}

.Pricing-Plan-Benefits-2X {
    color: #B9FD50;
    font-weight: bold;
}

.Pricing-Plan-Buy-Div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
}

.Pricing-Plan-Call-Button {
    /* width: 269px;
    height: 56px; */
    color: white;
    border: 1px solid #B9FD50;
    background-color: #22222290;
    border-radius: 8px;
    padding: 0.875rem;
    /* line-height: 22px; */
}
.Pricing-Plan-Call-Button:hover{
    background-color: #B9FD50;
    color: black;
    cursor: pointer;
}
.Pricing-Plan-Call-Button-Hover-1, .Pricing-Plan-Call-Button-Hover-2, .Pricing-Plan-Call-Button-Hover-3{
    background-color: #B9FD50;
    color: black;
}
.Pricing-Plan-Buy-Button {
    /* width: 269px;
    height: 56px; */
    color: black;
    border: 1px solid #B9FD50;
    background-color: #B9FD50;
    border-radius: 8px;
    padding: 0.875rem;
    /* line-height: 22px; */
}
.Pricing-Plan-Buy-Button:hover{
    background-color: transparent;
    color: #fff;
    cursor: pointer;
}
.Pricing-Plan-Buy-Button-Hover-1, .Pricing-Plan-Buy-Button-Hover-2, .Pricing-Plan-Buy-Button-Hover-3{
    background-color: transparent;
    color: white;
}
@media (max-width: 1440px) {
    .Pricing-Header h1{
        font-size: 48px;
    }
    .Pricing-Header p{
        font-size: 18px;
    }
}

@media (min-width: 1024px) and (max-width: 1439px) {
    .Pricing-Plan-One,
    .Pricing-Plan-Two,
    .Pricing-Plan-Three {
        padding: clamp(28px, 2.5vw, 40px);
    }
    .Pricing-Plan-Header h1{
        font-size: clamp(28px, 2.5vw, 34px);
    }
    .Pricing-Plan-Header span{
        font-size: clamp(10.4px, 1vw, 14px);
        padding-top: clamp(7px, 1vw, 10px);
        padding-bottom: clamp(7px, 1vw, 10px);
        padding-left: clamp(7px, 1.5vw, 20px);
        padding-right: clamp(7px, 1.5vw, 20px);
    }
    .Pricing-Plan-Header p{
        font-size: clamp(12px, 1vw, 14px);
    }
    .Pricing-Plan-Price h1{
        font-size: clamp(35px, 3.5vw, 48px);
    }
    .Pricing-Plan-Price p{
        font-size: clamp(12px, 1vw, 14px);    
    }
    .Pricing-Plan-Benefits p{
        font-size: clamp(16px, 1.5vw, 21px);
    }
    .Pricing-Plan-Buy-Div button{
        font-size: 14px;
    }
}

@media (max-width: 1024px) {
    html {
        font-size: 14px;
    }

    .Pricing-Header h1 {
        font-size: 42px;
    }

    .Pricing-Header p {
        font-size: 16px;
    }

    .Pricing-Plan-One,
    .Pricing-Plan-Two,
    .Pricing-Plan-Three {
        padding: 2rem;
    }

    .Pricing-Plan-Header h1 {
        font-size: 2rem;
    }

    .Pricing-Plan-Header-Feature-1 {
        font-size: 0.81rem;
        padding: 0.5rem 0.5rem;
        border-radius: 6px;
    }

    .Pricing-Plan-Header-Feature-2 {
        font-size: 0.81rem;
        padding: 0.5rem 0.5rem;
        border-radius: 6px;
    }

    .Pricing-Plan-Header p {
        font-size: 12px;
    }

    .Pricing-Plan-Price h1 {
        font-size: 2.5rem;
    }

    .Pricing-Plan-Price p {
        font-size: 12px;
    }
    .Pricing-Plan-Benefits p {
        font-size: 16px;
    }

    .Pricing-Plan-Buy-Div {
        margin-top: 5%;
        gap: 15px;
    }
    .Pricing-Plan-Buy-Button, .Pricing-Plan-Call-Button{
        font-size: 14px;
    }
}

@media (min-width: 768px) and (max-width:1023px) {
    html {
        font-size: 13px;
    }

    .Pricing-Plan-One,
    .Pricing-Plan-Two,
    .Pricing-Plan-Three {
        padding: 2.1rem;
    }

    .Pricing-Plan-Header h1 {
        font-size: 1.36rem;
    }

    .Pricing-Plan-Header-Feature-1 {
        font-size: 0.8rem;
        padding: 0.4rem 0.4rem;
        border-radius: 5px;
    }

    .Pricing-Plan-Header-Feature-2 {
        font-size: 0.8rem;
        padding: 0.4rem 0.4rem;
        border-radius: 5px;
    }

    .Pricing-Plan-Header p {
        font-size: 0.8rem;
        height: 41px;
    }

    .Pricing-Plan-Price h1 {
        font-size: 2.1rem;
    }

    .Pricing-Plan-Benefits p {
        font-size: 0.9rem;
        margin-left: 2%;
    }

    .Pricing-Plan-Buy-Div {
        margin-top: 5%;
        gap: 10px;
    }
}

@media (max-width: 768px) {
    html {
        font-size: 12px;
    }

    .Pricing-Header h1 {
        font-size: 36px;
    }

    .Pricing-Header p {
        font-size: 16px;
    }

    .Pricing-Plan-One,
    .Pricing-Plan-Two,
    .Pricing-Plan-Three {
        padding: 2rem;
    }

    .Pricing-Plan-Header h1 {
        font-size: 1.64rem;
    }

    .Pricing-Plan-Header-Feature-1 {
        border-radius: 5px;
        font-size: 0.8rem;
        padding: 0.5rem 0.5rem;
    }

    .Pricing-Plan-Header-Feature-2 {
        border-radius: 5px;
        font-size: 0.8rem;
        padding: 0.5rem 0.5rem;
    }

    .Pricing-Plan-Header p {
        font-size: 0.8rem;
    }

    .Pricing-Plan-Price h1 {
        font-size: 2rem;
    }

    .Pricing-Plan-Price p {
        font-size: 0.8rem;
    }

    .Pricing-Plan-Benefits p {
        font-size: 1rem;
        margin-left: 3%;
    }

    .Pricing-Plan-Buy-Div {
        margin-top: 5%;
        gap: 10px;
    }
}

@media (min-width: 425px) and (max-width: 767px) {
    .Pricing-Header h1 {
        font-size: 30px;
    }

    .Pricing-Header p {
        font-size: 14px;
    }

    .Pricing-Plans {
        grid-template-columns: 70%;
        margin-top: 20%;
    }

    .Pricing-Plan-Header h1 {
        font-size: 24px;
    }

    .Pricing-Plan-Header p {
        font-size: clamp(12px, 2vw, 16px);
    }

    .Pricing-Plan-Price h1 {
        font-size: 30px;
    }

    .Pricing-Plan-Price p {
        font-size: clamp(12px, 2vw, 16px);
    }

    .Pricing-Plan-Benefits p {
        font-size: clamp(12px, 3vw, 18px);
    }

    .Pricing-Plan-Buy-Div button {
        font-size: clamp(12px, 3vw, 18px);
    }

}

@media (max-width: 426px) {
    .Pricing-Header h1 {
        font-size: 2rem;
    }

    .Pricing-Plans {
        grid-template-columns: 90%;
        margin-top: 20%;
    }

    .Pricing-Plan-Header h1 {
        font-size: 2rem;
    }

    .Pricing-Plan-Header p {
        font-size: 1rem;
    }

    .Pricing-Plan-Price h1 {
        font-size: 2.5rem;
    }

    .Pricing-Plan-Price p {
        font-size: 1rem;
    }

    .Pricing-Plan-Benefits p {
        font-size: 1.1rem;
    }

    .Pricing-Plan-Buy-Div button {
        font-size: 1rem;
    }
}