html{
    font-size: 16px;
}
.FAQ-Section {
    margin: 0 auto;
    margin-top: 8%;
    background-color: white;
    width: 90%;
    padding-bottom: 20%;
}

.FAQ-Header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 3%;
}

.FAQ-Header h1 {
    font-size: 3rem;
    color: #111204;
    margin-bottom: 0%;
}

.FAQ-Header p {
    font-size: 1.125rem;
    color: rgba(0, 0, 0, 0.6);
}

.FAQ-DropDown {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.FAQ-DropDown-Question {
    width: 45%;
    background-color: #111204;
    color: white;
    margin-top: 2%;
    border-radius: 16px;
}

.FAQ-DropDown-Question p {
    padding: 0.625rem 0.625rem 0.625rem 1.875rem;
    font-size: 1.125rem;
}

.FAQ-DropDown-Inline-Div {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}
.FAQ-DropDown-Inline-Arrow{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    transform: rotate(90deg);
    cursor: pointer;
    font-size: 2rem;
    background-color: #B9FD50;
    color: #111204;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5%;
}
.FAQ-DropDown-Inline-Arrow:hover{
    background-color: transparent;
    border: 1px solid #B9FD50;
    color: #B9FD50;
}
.FAQ-DropDown-Inline {
    display: inline-block;
}
.FAQ-DropDown-Hidden {
    display: none;
}

.FAQ-DropDown-Show {
    display: block;
    margin-top: -1%;
}
.FAQ-Query{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2%;
}
.FAQ-Query textarea{
    width: 45%;
    height: 132px;
    border-radius: 0.875rem;
    border: 2px solid #111204;
    padding: 0.625rem; /* Optional: Add padding for better text alignment */
    box-sizing: border-box; /* Ensures padding is included in width/height */
    resize: none; /* Disable resizing if you want a fixed size */
    font-family: inherit;
}
.FAQ-Query-Button{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 45%;
    margin-top: 2%;
}
.FAQ-Query-Button p{
    width: 50%;
    font-size: 0.875rem;
    color: rgba(17, 18, 4, 0.6);
}
.FAQ-Query-Button button{
    width: 27%;
    height: 48px;
    border-radius: 30px;
    font-weight: bold;
    background-color: #B9FD50;
    border: none;
}
.FAQ-Query-Button button:hover{
    background-color: black;
    color: #B9FD50;
    cursor: pointer;
}
@media (max-width: 1024px) {
    html{
        font-size: 14px;
    }
    .FAQ-Header h1{
        font-size: 42px;
    }
    .FAQ-Header p{
        font-size: 16px;
    }
    .FAQ-Section{
        padding-bottom: 30%;
    }
    .FAQ-DropDown-Question{
        width: 50%;
    }
    .FAQ-DropDown-Question p{
        font-size: 16px;
    }
    .FAQ-Query textarea{
        width: 50%;
    }
    .FAQ-Query textarea{
        font-size: 16px;
    }
    .FAQ-Query-Button{
        width: 50%;
    }
    .FAQ-Query-Button button{
        font-size: 14px;
    }
    .FAQ-Query-Button p{
        font-size: 12px;
    }
}
@media (min-width: 1024px) and (max-width: 1439px) {
    .FAQ-DropDown-Question p{
        font-size: clamp(16px, 1.2vw, 18px);
    }
}
@media (min-width: 768px) and (max-width: 1023px) {
    html{
        font-size: 13px;
    }
    .FAQ-DropDown-Question{
        width: 60%;
    }
    .FAQ-Query textarea{
        width: 60%;
    }
    .FAQ-Query-Button{
        width: 60%;
    }
}

@media (max-width: 768px) {
    html{
        font-size: 12px;
    }
    .FAQ-Header h1{
        font-size: 36px;
    }
    .FAQ-Header p{
        font-size: 16px;
    }
    .FAQ-DropDown-Question{
        width: 60%;
    }
    .FAQ-Query textarea{
        width: 60%;
    }
    .FAQ-Query-Button{
        width: 60%;
    }
}
@media (min-width: 425px) and (max-width: 767px) {
    .FAQ-Header h1{
        font-size: 30px;
    }
    .FAQ-Header p{
        font-size: 14px;
    }
    .FAQ-DropDown-Question{
        width: 80%;
    }
    .FAQ-DropDown-Question p{
        font-size: clamp(12px, 3vw, 13.5px);
    }
    .FAQ-DropDown-Inline-Arrow{
        font-size: clamp(12px, 3vw, 24px);
        height: clamp(25px, 7vw, 50px);
        width: clamp(25px, 7vw, 50px);
    }
    .FAQ-Query textarea{
        width: 80%;
        font-size: clamp(12px, 3vw, 13.5px);
    }
    .FAQ-Query-Button{
        width: 80%;
    }
}
@media (max-width: 426px) {
    .FAQ-Section{
        width: 100%;
    }
    .FAQ-Header h1{
        font-size: 2rem;
    }
    .FAQ-Header p{
        font-size: 1rem;
    }
    .FAQ-DropDown-Question{
        width: 80%;
    }
    .FAQ-DropDown-Question p{
        font-size: 1rem;
    }
    .FAQ-DropDown-Inline-Arrow{
        font-size: 1rem;
        height: 25px;
        width: 25px;
    }

    .FAQ-Query textarea{
        width: 80%;
        font-size: 12px;
    }
    .FAQ-Query-Button{
        width: 80%;
    }
    .FAQ-Query-Button p{
        width: 60%;
    }
    .FAQ-Query-Button button {
        width: 30%;
        height: 39px;
        border-radius: 20px;
        font-size: 12px;
    }
}