html {
  font-size: 16px;
  scroll-behavior: smooth;
}
body{
  background-color: #111204;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 1440px;
  margin: 0 auto;
}

.App-header {
  flex: 1;
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 2.75rem 6rem 0rem 6rem;
  background-image: url('./Ellipse 3.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  background-size: 56.25rem 56.25rem;
  margin: 0 auto;
  margin-bottom: 10%;
}

.Mobile-Menu-Button {
  display: none;
}

.App-header li a, .Footer-Menu li a {
  text-decoration: none;
  color: #FFFFFF;
}
.App-header li a:hover, .Footer-Menu li a:hover{
  color: #B9FD50;
}
.App-header li a:active, .Footer-Menu li a:active{
  color: #B9FD50;
}
.App-header ul,
.Footer-Menu ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
}

.Logo-Div {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.Menu-Div {
  display: flex;
  gap: 3.75rem;
}

.Button {
  background-color: #B9FD50;
  color: #111204;
  padding: 1.375rem 0rem;
  width: 10rem;
  height: 3.5rem;
  font-weight: medium;
  font-size: 1rem;
  border-radius: 8px;
  border: none;
  margin-left: 1.875rem;
}
.Button:hover{
  background-color: #111204;
  color: #B9FD50;
  border: 1px solid #B9FD50;
  cursor: pointer;
}
.App-Footer {
  background-color: #111204;
}

.Footer-Top {
  /* height: 424px; */
  width: 80%;
  background-color: #1E2008;
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url('./Ellipse 3.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1000px;
  border: none;
  border-radius: 35px;
  margin-top: -12%;
  padding: 0%;
  box-sizing: border-box;
}

.Footer-Top h1 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: bold;
  text-align: center;
  margin-top: 5%;
  line-height: 4rem;
  letter-spacing: -5px;
}

.Footer-Top p {
  color: #FFFFFF;
  font-size: 1.125rem;
  text-align: center;
  margin-top: -2%;
}

.Footer-Top button {
  margin-top: 2%;
  margin-left: 0rem;
  margin-bottom: 5%;
}
.Footer-Top button:hover{
  background-color: transparent;
  color: #FFFFFF;
}
.Footer-Content {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
  margin-top: 8%;
}

.Footer-Text {
  text-align: left;
  width: 40%;
}

.Footer-Text p {
  color: #FFFFFF;
  width: 100%;
  font-size: 1rem;
}

.Footer-Contact p {
  color: #FFFFFF;
  font-size: 1rem;
}

.Footer-Social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: -5%;
  height: 80px;
}

.Footer-Social p:hover {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #B9FD50;
  color: #111204;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  cursor: pointer;
}

.Footer-Menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #FFFFFF;
  width: 70%;
  margin: 0 auto;
}

.Footer-Menu ul {
  padding-left: 0%;
}

.Footer-Line {
  margin: 0 auto;
  width: 80%;
  height: 1px;
  color: #404040;
  background-color: #404040;
}

@media (max-width: 1024px) {
  html {
    font-size: 14px;
  }

  .App-header {
    padding: 44px 0px 0px 0px;
    box-sizing: border-box;
  }

  .Logo-Div {
    padding-left: 44px;
    box-sizing: border-box;
  }

  .Menu-Div {
    padding-right: 44px;
    box-sizing: border-box;
  }
  .Footer-Top p{
    font-size: 16px;
  }
  .Footer-Text {
    width: 50%;
  }

  .Footer-Text p {
    width: 80%;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  html {
    font-size: 13px;
  }

  .Footer-Text {
    width: 75%;
  }

  .Footer-Text p {
    width: 60%;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 12px;
  }

  .Footer-Text {
    width: 75%;
  }

  .Footer-Text p {
    width: 60%;
  }
}

@media (min-width: 425px) and (max-width: 767px) {
  .App-header {
    margin-bottom: 5rem;
  }

  .Logo-Div {
    padding-left: 20px;
  }

  .Mobile-Menu-Button {
    display: block;
    padding-right: 20px;
    cursor: pointer;
  }

  .Menu-Div {
    display: none;
    margin-top: 35px;
    position: absolute;
    overflow-y: auto;
    z-index: 1000;
    background-color: #111204;
    /* left: 0; */
  }

  .Menu-Div button {
    margin-left: 0%;
    margin-top: -5%;
    margin-bottom: 25px;
  }

  .Show-Mobile-Menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0%;
  }

  .App-header ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0%;
    padding-top: 25px;
  }
  .Footer-Top {
    margin-top: clamp(-30%, 18vw, -18%);
    border-radius: 20px;
  }

  .Footer-Top h1 {
    font-size: clamp(30px, 6vw, 52px);
    letter-spacing: clamp(-3px, 6vw, -5px);
    line-height: clamp(2.5rem, 6vw, 4rem);
    margin-top: 30px;
  }

  .Footer-Top p {
    font-size: clamp(12px, 2vw, 13.5px);
  }

  .Footer-Top button {
    margin-bottom: 30px;
    font-size: 1rem;
    width: clamp(8rem, 18vw, 10rem);
    height: 3.5rem;
    text-align: center;
  }

  .Footer-Content {
    flex-direction: column;
  }

  .Footer-Menu {
    justify-content: center;
    align-items: center;
  }

  .Footer-Menu ul {
    display: none;
  }

  .Footer-Text {
    width: 100%;
  }

  .Footer-Text p {
    font-size: 1rem;
    width: 100%;
  }

  .Footer-Contact {
    width: 60%;
  }

  .Footer-Social {
    margin-top: 0%;
    align-items: center;
    height: 50px;
    width: 179px;
  }
}

@media (max-width: 426px) {
  html {
    font-size: 12px;
  }

  .App-header {
    margin-bottom: 5rem;
    background-size: 40.25rem 40.25rem;
  }

  .Logo-Div {
    padding-left: 20px;
  }

  .Mobile-Menu-Button {
    display: block;
    padding-right: 20px;
    cursor: pointer;
  }

  .Menu-Div {
    display: none;
    margin-top: 12%;
    position: absolute;
    overflow-y: auto;
    z-index: 1000;
    background-color: #111204;
    /* left: 0; */
  }

  .Menu-Div button {
    margin-left: 0%;
    margin-top: -8%;
    margin-bottom: 10%;
  }

  .Show-Mobile-Menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0%;
  }

  .App-header ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0%;
    padding-top: 10%;
  }

  .Footer-Top {
    margin-top: -28%;
    border-radius: 20px;
  }

  .Footer-Top h1 {
    font-size: 2.5rem;
    letter-spacing: -3px;
    line-height: 2.5rem;
    margin-top: 10%;
  }

  .Footer-Top p {
    font-size: 1rem;
  }

  .Footer-Top button {
    margin-bottom: 10%;
    font-size: 1rem;
    width: 8rem;
    height: 3.5rem;
    text-align: center;
  }

  .Footer-Content {
    flex-direction: column;
  }

  .Footer-Menu {
    justify-content: center;
    align-items: center;
  }

  .Footer-Menu ul {
    display: none;
  }

  .Footer-Text {
    width: 100%;
  }

  .Footer-Text p {
    font-size: 1rem;
    width: 100%;
  }

  .Footer-Contact {
    width: 60%;
  }

  .Footer-Social {
    margin-top: 0%;
    align-items: center;
    height: 50px;
  }
}