html {
    font-size: 16px;
}

.Testimonials {
    width: 90%;
    background-color: white;
    color: #111204;
    margin: 0 auto;
    margin-top: 8%;
    padding-bottom: 15%;
}

.Testimonial-Header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 5%;
}

.Testimonial-Header h3 {
    font-size: 1rem;
    font-weight: 400;
    margin-bottom: -2%;
}

.Testimonial-Header h1 {
    font-size: 3rem;
    font-weight: bold;
    text-align: center;
}

.Testimonial-Header-Heading-SecondLine {
    margin-top: -3%;
    margin-bottom: 0%;
}

.Testimonial-Header p {
    font-size: 1.125rem;
    text-align: center;
}

.Testimonial-Header-Para-SecondLine {
    margin-top: -1%;
}

.Testimonial-SlideShow {
    background-color: #f5f5f5;
    justify-self: center;
    width: 75%;
    height: auto;
    border-radius: 35px;
    position: relative;
    margin-top: 5%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.left-arrow,
.right-arrow {
    position: absolute;
    top: 50%;
    /* transform: translateY(-50%); */
    background-color: #FFFFFF;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
    border: none;
    cursor: pointer;
    padding: 1%;
    border-radius: 50%;
    font-size: 1.375rem;
    color: #111204;
    height: 64px;
    width: 64px;
}

.left-arrow {
    left: -10%;
}

.right-arrow {
    right: -10%;
}

.Testimonial-SlideShow1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    /* gap: 20px; */
    padding: 4.8125rem;
}

.Testimonial-SlideShow-Text {
    width: 60%;
}

.Testimonial-SlideShow-Text h3 {
    font-size: 1.625rem;
    /* line-height: 31px; */
    font-weight: bold;
    margin-bottom: 10%;
}

.Testimonial-SlideShow-Text p {
    font-size: 1.125rem;
    /* line-height: 26px; */
    width: 90%;
    margin-top: -5%;
}

.Testimonial-SlideShow-Text h4 {
    font-size: 1.25rem;
    font-weight: bold;
}

.Testimonial-SlideShow-Picture img {
    height: auto;
    width: 100%;
}

@media (max-width: 1440px) {
    .Testimonial-Header h1{
        font-size: 48px;
    }
    .Testimonial-Header p{
        font-size: 18px;
    }
}

@media (min-width: 1024px) and (max-width: 1439px) {
    .Testimonial-SlideShow-Text h3{
        font-size: clamp(18px, 1.8vw, 26px);
    }   
    .Testimonial-SlideShow-Text p{
        font-size: clamp(16px, 1.5vw, 18px);
    }
    .Testimonial-SlideShow-Text h4{
        font-size: clamp(16.8px, 1.5vw, 20px);
    }
}
@media (max-width: 1024px) {
    html {
        font-size: 14px;
    }
    .Testimonial-Header h1{
        font-size: 42px;
    }
    .Testimonial-Header p{
        font-size: 16px;
    }
    .left-arrow {
        left: -12%;
    }

    .right-arrow {
        right: -12%;
    }

    .Testimonial-SlideShow1 {
        padding: 3rem;
    }

    .Testimonial-SlideShow-Text {
        width: 70%;
    }

    .Testimonial-SlideShow-Text h3 {
        font-size: 1.3rem;
        font-weight: bold;
    }

    .Testimonial-SlideShow-Text p {
        font-size: 16px;
        width: 98%;
    }

    .Testimonial-SlideShow-Text h4 {
        font-size: 1.2rem;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    html {
        font-size: 13px;
    }
}

@media (max-width: 768px) {
    html {
        font-size: 12px;
    }
    .Testimonial-Header h1{
        font-size: 36px;
    }
    .Testimonial-Header p{
        font-size: 16px;
    }

    .left-arrow {
        left: -15%;
    }

    .right-arrow {
        right: -15%;
    }

    .Testimonial-SlideShow1 {
        padding: 3rem;
    }

    .Testimonial-SlideShow-Text {
        width: 85%;
    }

    .Testimonial-SlideShow-Text h3 {
        font-size: 1.1rem;
        font-weight: bold;
    }

    .Testimonial-SlideShow-Text p {
        font-size: 1rem;
        width: 95%;
    }

    .Testimonial-SlideShow-Text h4 {
        font-size: 1.1rem;
    }
}

@media (min-width: 425px) and (max-width: 767px) {
    .Testimonials {
        width: 100%;
    }

    .Testimonial-Header h1 {
        font-size: 30px;
    }

    .Testimonial-Header-Heading-SecondLine {
        display: none;
    }

    .Testimonial-Header p {
        font-size: 14px;
        width: 90%;
    }

    .Testimonial-Header-Para-SecondLine {
        display: none;
    }

    .Testimonial-SlideShow1 {
        flex-direction: column;
        gap: 20px;
    }

    .left-arrow,
    .right-arrow {
        /* height: 30px;
        width: 30px;
        font-size: 1rem; */
        height: clamp(30px , 8vw, 64px);
        width: clamp(30px, 8vw, 64px);
        font-size: clamp(12px, 2vw, 16px);
    }

    .Testimonial-SlideShow-Text {
        order: 2;
        width: 100%;
    }

    .Testimonial-SlideShow-Text img {
        width: 40%;
        height: auto;
    }

    .Testimonial-SlideShow-Text h3 {
        width: 100%;
        font-size: 16px;
    }

    .Testimonial-SlideShow-Text p {
        width: 100%;
        font-size: 14px;
    }
    .Testimonial-SlideShow-Text p:last-child{
        margin-top: 0%;
    }
    .Testimonial-SlideShow-Picture {
        order: 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .Testimonial-SlideShow-Picture img {
        width: 50%;
        height: 50%;
    }
}

@media (max-width: 426px) {
    .Testimonials {
        width: 100%;
    }

    .Testimonial-Header h1 {
        font-size: 2rem;
    }

    .Testimonial-Header-Heading-SecondLine {
        display: none;
    }

    .Testimonial-Header p {
        font-size: 1rem;
        width: 90%;
    }

    .Testimonial-Header-Para-SecondLine {
        display: none;
    }

    .Testimonial-SlideShow1 {
        flex-direction: column;
        gap: 20px;
    }

    .left-arrow,
    .right-arrow {
        height: 30px;
        width: 30px;
        font-size: 1rem;
    }

    .Testimonial-SlideShow-Text {
        order: 2;
        width: 100%;
    }

    .Testimonial-SlideShow-Text img {
        width: 40%;
        height: auto;
    }

    .Testimonial-SlideShow-Text h3 {
        width: 100%;
    }

    .Testimonial-SlideShow-Text p {
        width: 100%;
    }

    .Testimonial-SlideShow-Picture {
        order: 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .Testimonial-SlideShow-Picture img {
        width: 50%;
        height: 50%;
    }
}