html{
    font-size: 16px;
}
.Blog-Page{
    margin: 0 auto;
    margin-top: 8%;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 1440px;
}
.Blog-Header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Blog-Header h4{
    color: #B9FD50;
    margin-bottom: -7%;
}
.Blog-Header h1{
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 0%;
    color: white;
}
.Blog-Header p{
    font-size: 1.125rem;
    text-align: center;
    color: white;
}
.Blog-Grid{
    display: grid;
    grid-template-columns: 46% 46%;
    grid-template-rows: auto auto auto;
    place-items: center;
    place-content: center;
    gap: 20px;
    margin-top: 8%;
    cursor: pointer;
}
.Blogs{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.Blogs-Image img{
    width: auto;
    height: 100%;
}
.Blogs-Text{
    border: 1px solid rgba(185, 253, 80, 0.37);
    border-radius: 16px;
    border-left: none;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    margin-left: -13px;
    padding-left: 10%;
    /* padding-right: 20px; */
}
.Blogs-Text h4{
    color: #B9FD50;
    font-size: 0.875rem;
    font-weight: bold;
    width: 30%;
}
.Blogs-Text h2{
    font-size: 1.5rem;
    font-weight: bold;
    color: white;
    width: 70%;
}
.Blogs-Text p{
    font-size: 1rem;
    color: #ABABB2;
    width: 80%;
}
.More-Blog-Button{
    background-color: transparent;
    color: #B9FD50;
    border: 1px solid #B9FD50;
    padding: 1.5rem 3rem;
    border-radius: 16px;
    font-size: 0.875rem;
    font-weight: bold;
    margin-top: 8%;
}
.More-Blog-Button:hover{
    cursor: pointer;
    background-color: #B9FD50;
    color: black;
}
@media (min-width: 1024px) and (max-width: 1439px) {
    .Blog-Header h1{
        font-size: 48px;
    }
    .Blog-Header p{
        font-size: 18px;
    }
    .Blogs-Image img{
        width: 100%;
    }
    .Blogs-Text h4{
        width: 100%;
    }
    .Blogs-Text h2{
        font-size: clamp(21px, 2vw, 26px);
        width: 80%;
    }
    .Blogs-Text p{
        font-size: clamp(16px, 1.2vw, 18px);
        width: 95%;
    }
}

@media (max-width: 1024px) {
    html{
        font-size: 14px;
    }
    .Blog-Header h1{
        font-size: 42px;
    }
    .Blog-Header p{
        font-size: 16px;
    }
    .Blogs-Image img{
    width: 100%;
    }
    .Blogs-Text h4{
        width: 50%;
    }
    .Blogs-Text h2{
        width: 90%;
        margin-top: -1%;
    }
    .Blogs-Text p{
        font-size: 1rem;
        color: #ABABB2;
        width: 95%;
        margin-top: -2%;
    }
    .More-Blog-Button{
        font-size: 14px;
    }
}
@media (min-width: 768px) and (max-width: 1023px) {
    html{
        font-size: 13px;
    }
    .Blogs-Image img{
    width: 100%;
    }
    .Blogs-Text h4{
        width: 50%;
    }
    .Blogs-Text h2{
        font-size: 1.24rem;
        width: 90%;
        margin-top: -1%;
    }
    .Blogs-Text p{
        font-size: 1rem;
        color: #ABABB2;
        width: 95%;
        margin-top: -2%;
    }
    
}
@media (max-width: 768px) {
    html{
        font-size: 13px;
    }
    .Blog-Header h1{
        font-size: 36px;
    }
    .Blog-Header p{
        font-size: 16px;
    }
    .Blogs-Image img{
        width: 100%;
        }
    .Blogs-Text h2{
        font-size: 1.2rem;
        width: 90%;
        margin-top: -1%;
    }
    .Blogs-Text p{
        font-size: 1rem;
        width: 95%;
        margin-top: -2%;
    }
}
@media (min-width: 425px) and (max-width: 767px) {
    .Blog-Header h1{
        font-size: 30px;
    }
    .Blog-Header p{
        font-size: 14px;
    }
    .Blog-Grid{
        grid-template-columns: 70%;
    } 
}
@media (max-width: 426px) {
    .Blog-Header h1{
        font-size: 2rem;
    }
    .Blog-Header p{
        font-size: 1rem;
    }
    .Blog-Grid{
        grid-template-columns: 90%;
    }
    .More-Blog-Button{
        border-radius: 12px;
        font-size: 1rem;
    }
}